import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';
import { dividerStyles } from './dividerStyle';
import { DividerPadding, DividerProperties } from './dividerProperties';
import { version } from './metadata';

@defineElement({ tagName: 'divider', styles: dividerStyles, version })
export class Divider extends BaseElement implements DividerProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter }) vertical = false;
    @property({ type: Boolean, reflect: true, attribute: 'flex-item', converter: booleanConverter }) flexItem = false;
    @property({ type: DividerPadding }) padding = DividerPadding.None;

    protected render() {
        const [,] = createClassName(this.tagName.toLowerCase(), {
            [this.padding]: true,
        });
        return html``;
    }
}
